.page {
  scroll-behavior: smooth;

  &__body {
    font-family: $_raleway;
    color: $colorBlack;
    background-color: $colorWhite;

    &--with-menu {
      overflow: hidden;
    }
  }

  &__header {
    margin-bottom: 90px;

    @include onTablet {
      margin-bottom: 100px;
    }

    @include onDesktop {
      margin-bottom: 110px;
    }
  }

  &__menu {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;

    transform: translateX(100%);
    transition: transform $smoothTime linear;

    &:target {
      transform: translateX(0);
    }
  }

  &__actual {
    margin-bottom: 120px;

    @include onTablet {
      margin-bottom: 160px;
    }

    @include onDesktop {
      margin-bottom: 200px;
    }
  }

  &__nearest {
    margin-bottom: 90px;

    @include onTablet {
      margin-bottom: 100px;
    }

    @include onDesktop {
      margin-bottom: 110px;
    }
  }

  &__visit {
    margin-bottom: 90px;

    @include onTablet {
      margin-bottom: 125px;
    }

    @include onDesktop {
      margin-bottom: 110px;
    }
  }

  &__news {
    margin-bottom: 120px;

    @include onTablet {
      margin-bottom: 160px;
    }

    @include onDesktop {
      margin-bottom: 200px;
    }
  }

  &__subsribe {
    margin-bottom: 80px;

    @include onTablet {
      margin-bottom: 120px;
    }

    @include onDesktop {
      margin-bottom: 200px;
    }
  }
}
